<template>
  <div class="result">
    <!-- <Title>[]</Title> -->

    <Header>[]</Header>
    <h4>:</h4>
    <div class="three" v-for="(item, index) in winner" v-bind:key="'winner' + index">{{ item }}</div>
    <h4>:</h4>
    <div class="three" v-for="(item, index) in loser" v-bind:key="'loser' + index">{{ item }}</div>
    <v-row justify="center">
      <v-btn color="primary" dark @click.stop="dialog = true" absolute right bottom></v-btn>

      <v-dialog v-model="dialog" max-width="290">
        <v-card>
          <v-card-title class="headline">
            <p class="ma-auto">[]</p>
          </v-card-title>

          <v-card-text>
            <div v-for="(item, index) in ranking" :key="'ranking' + index">{{ item }}</div>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn color="green darken-1" text @click="dialog = false"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
    <v-btn
      depressed
      large
      color="primary"
      class="btn1"
      to="/wait"
      v-on:click.native="restartGame"
    ></v-btn>
  </div>
</template>

<style scoped>
.three {
  border: 1px solid black;
  width: 200px;
  margin: 20px auto;
}
h4 {
  margin-top: 50px;
}
.v-btn {
  margin-top: 30px;
}
.v-btn--absolute.v-btn--bottom, .v-btn--fixed.v-btn--bottom {
    bottom: 80px;
}
</style>

<script>
// import Title from "@/components/Title.vue";
import Header from "@/components/Header.vue";
export default {
  data() {
    return {
      dialog: false
      // winner: ["aaa - ", "bbb - ", "ccc - ", "ddd - "],
      // loser: ["eee - ", "fff - "],
      // rank: [
      //   "aaa ",
      //   "bbb ",
      //   "ccc ",
      //   "ddd ",
      //   "eee ",
      //   "fff ",
      // ],
    };
  },
  computed: {
    name: function() {
      return this.$store.state.gameInit.name;
    },
    isAbort: function() {
      return this.$store.state.gameStatus.isAbort;
    },
    winGroup: function() {
      return this.$store.state.ranking.winGroup;
    },
    loseGroup: function() {
      return this.$store.state.ranking.loseGroup;
    },
    rankings: function() {
      return this.$store.state.ranking.rankings;
    },
    winner: function() {
      let tmp = [];
      for (let item of this.winGroup) {
        tmp.push(item.name + " - " + item.identity);
      }
      return tmp;
    },
    loser: function() {
      let tmp = [];
      for (let item of this.loseGroup) {
        tmp.push(item.name + " - " + item.identity);
      }
      return tmp;
    },
    ranking: function() {
      let tmp = [];
      for (let item of this.rankings) {
        tmp.push(item.name + "  " + +item.winTimes + "");
      }
      return tmp;
    }
  },
  mounted() {
    if (this.$store.state.gameInit.name !== "_God") {
      this.$store.dispatch("ranking/updateRankings");
    } else {
      this.$store.dispatch("ranking/sortedList");
    }
  },
  methods: {
    restartGame: function() {
      this.$store.state.gameInit.currentPlayerNum = 0;
      if (this.name === "_God") {
        this.$store.dispatch("gameInit/startGame");
        // console.log("what's up bro");
      } else {
        this.$store.dispatch("gameInit/joinRoom", {
          roomID: this.$store.state.gameInit.roomID,
          name: this.$store.state.gameInit.name
        });
      }
    }
  },
  components: {
    // Title,
    Header
  }
};
</script>
